<template>
  <div class="detail-content">
    <div class="content-top">
      <h1 class="title">
        
      </h1>
      <div class="detail-blue">
        
      </div>
      <div class="spec">
        <div>
          <div class="spec-left">
            
          </div>
          <div class="spec-right">
            <v-counter @on-change="calc"></v-counter>
          </div>
        </div>
        <div>
          <div class="spec-left">
            
          </div>
          <div class="spec-right">
            <v-chooser :selections="expTime" @on-change="calc"></v-chooser>
          </div>
        </div>
        <div>
          <div class="spec-left">
            
          </div>
          <div class="spec-right">
            <v-multi-chooser :selections="versions" @on-change="calc"></v-multi-chooser>
          </div>
        </div>
        <div>
          <div class="spec-left">
            
          </div>
          <div class="spec-right">
            {{ amount }}
          </div>
        </div>
        <div>
          <div class="spec-left">
          </div>
          <div class="spec-right">
            <a class="buy-now" @click="buyNow"></a>
          </div>
        </div>
      </div>
    </div>
    <div class="content-bottom">
      <h1 class="title">
        
      </h1>
      <div class="body">
        <p>
          2020    
        </p>
      </div>
    </div>
    <my-dialog :is-show="isShowBankDialog" @close="close('isShowBankDialog')">
      <table class="paylist">
        <tr>
          <th></th>
          <th></th>
          <th></th>
          <th></th>
        </tr>
        <tr>
          <td>{{num}}</td>
          <td>{{expTime[timeIndex].label}}</td>
          <td>{{verPrice.label.join(',')}}</td>
          <td>{{amount}}</td>
        </tr>
      </table>
      <div class="bank-head"></div>
      <bank class="bank"></bank>
      <a class="confirm btn" @click="confirmBuy"></a>
    </my-dialog>
    <my-dialog :is-show="isShowAskDialog" @close="close('isShowAskDialog')">
      <p class="ask">
        <a class="btn success" @click="ask(true)"></a>
        <a class="btn failed" @click="ask(false)"></a>
      </p>
    </my-dialog>
    <my-dialog :is-show="isShowFinDialog" @close="close('isShowFinDialog')">
      <p class="success">
        
      </p>
    </my-dialog>
  </div>
</template>

<script>
  import vCounter from '@/components/base/counter'
  import vSelect from '@/components/base/select'
  import vChooser from '@/components/base/chooser'
  import vMultiChooser from '@/components/base/multiChooser'
  import myDialog from '@/components/base/Dialog'
  import bank from '@/components/bank'

  export default {
    components: {
      vCounter,
      vSelect,
      vChooser,
      vMultiChooser,
      myDialog,
      bank
    },
    data () {
      return {
        num: 1,
        productIndex: 0,
        timeIndex: 0,
        verIndex: [0],
        isShowBankDialog: false,
        isShowAskDialog: false,
        isShowFinDialog: false,
        productTypes: [
          {
            label: '',
            value: 0,
            price: 0
          },
          {
            label: '',
            value: 1,
            price: 120
          },
          {
            label: '',
            value: 2,
            price: 240
          },
          {
            label: '',
            value: 3,
            price: 340
          }
        ],
        expTime: [
          {
            label: '',
            value: 0,
            price: 0
          },
          {
            label: '',
            value: 1,
            price: 100
          },
          {
            label: '',
            value: 2,
            price: 200
          }
        ],
        versions: [
          {
            label: '',
            value: 0,
            price: 100
          },
          {
            label: '',
            value: 1,
            price: 230
          },
          {
            label: '',
            value: 2,
            price: 400
          }
        ]
      }
    },
    computed: {
      productPrice () {
        return this.productTypes[this.productIndex].price
      },
      timePrice () {
        return this.expTime[this.timeIndex].price
      },
      verPrice () {
        let price = 0
        let label = []
        for (let i = 0; i < this.verIndex.length; i++) {
          let id = this.verIndex[i]
          price += this.versions[id].price
          label.push(this.versions[id].label)
        }
        return {
          price,
          label
        }
      },
      amount () {
        return this.num * (this.productPrice + this.timePrice + this.verPrice.price)
      }
    },
    methods: {
      calc (param) {
        let type = param[0]
        let number = param[1]

        if (type === 'counter') {
          this.num = number
        } else if (type === 'select') {
          this.productIndex = number
        } else if (type === 'chooser') {
          this.timeIndex = number
        } else if (type === 'multiChooser') {
          this.verIndex = number
        }
      },
      buyNow () {
        this.isShowBankDialog = true
      },
      close (attr) {
        this[attr] = false
      },
      confirmBuy () {
        this.isShowBankDialog = false
        this.isShowAskDialog = true
      },
      ask (flag) {
        if (flag) {
          this.isShowFinDialog = true
        }
        this.isShowAskDialog = false
      }
    }
  }
</script>

<style scoped>

</style>



